<template>
  <div class="payment">
      
      <div class="payment-box">
          <div class="payment-page">

              <div class="payment-item-box">
                  <div class="payment-item">
                      <div class="cart-item-price">
                          <div class="cart-price">
                              <p>
                                전체 금액 : 
                              </p>
                              <span class="red">
                                    {{itemPay}} 원    
                                </span>
                              
                          </div>
                      </div>
                      <div class="delivery-item-price">
                          <div class="deliver-price">
                              <p>
                                  배송비 :
                                  
                              </p>
                              <span class="red">
                                {{deliPay}} 원
                              </span>
                          </div>
                      </div>
                      <div class="total-price">
                          <div class="total">
                              <p>
                                  총액 :
                              </p>
                              <span class="red">
                                   {{totalPay}} 원
                              </span>
                              
                          </div>
                      </div>
                      
                  </div>
              </div>

              <div class="payment-name">
                  <p>받으시는 분</p>
                  <input type="text">
              </div>
              <div class="payment-phone">
                  <p>
                      전화번호
                  </p>
                  <div class="phone">
                    <input type="text" v-model="tel1">
                    <p>-</p>
                    <input type="text" v-model="tel2">
                    <p>-</p>
                    <input type="text" v-model="tel3">
                  </div>
                  
                  <div class="phone-btn-box" v-if="phonCodebtn && tel1 != '' && tel2 != '' && tel3 != ''">
                      <div class="phone-btn">
                          <button @click="phonecodeSend">인증 요청</button>
                      </div>
                  </div>
                  <div class="phone-btn-box notselect" v-else-if="!phoneCodebtn && tel3 == ''">
                      <div class="phone-btn">
                          <button @click="phonecodeSend">인증 요청</button>
                      </div>
                  </div>

                  <div class="phone-code-box" v-else-if="tel1 != '' && tel2 != '' && tel3 != ''">
                      <p>인증 코드 입력</p>
                      <div class="phone-code">
                          <input type="text" placeholder="인증코드를 입력해 주세요" v-model="codeInput">
                          <span>
                              {{showTime}}
                          </span>
                      </div>
                      <div class="phone-btn" >
                            <button v-if="count >= 0" @click="phoneCode">
                                인증코드 입력
                            </button>
                            <button v-if="count < 0" @click="recount">
                                인증코드 재발급
                            </button>
                      </div>
                      <div class="phone-code-succ" v-if="codeSucc == 1">
                          <p>
                              인증에 성공하였습니다.
                          </p>
                      </div>
                      <div class="phone-code-succ" v-if="codeSucc == 2">
                          <p class="red">
                              인증에 실패하였습니다.
                          </p>
                      </div>
                      <div class="phone-code-succ" v-if="codeSucc == 3">
                          <p class="red">
                              인증에 시간이 초과되었습니다. 다시 시도해주세요
                          </p>
                      </div>
                  </div>
                  
              </div>

              <div class="payment-address-box">
                  <div class="payment-address">
                      <p>주소입력</p>
                      <div class="address-chk-box">
                          <div class="address-chk" v-for="(clk, i) in actbtn" :key="i">
                              <button :class="{'noclickbtn': !clk.clk, 'clickbtn': clk.clk}" @click="addresscho(i)">
                                  {{clk.val}}
                              </button>
                           </div>
                      </div>
                      <div class="address-input">
                          <input type="checkbox" id="address-save" v-model="addrchk">
                          <label for="address-save">
                              <p>기본 배송지로 사용하기</p>
                          </label>
                          <div class="address-box">
                            <input type="text" placeholder="우편번호" id="address-code" v-model="addcode">
                            <button @click="findAdd">주소찾기</button>
                            <input type="text" placeholder="주소입력" v-model="add1">
                            <input type="text" placeholder="상세주소" v-model="add2">
                          </div>

                          <button class="saveaddr" v-if="notuse">
                              이 주소 다음에도 사용하기
                          </button>
                      </div>
                      <transition name="daum">
                          <div class="daumadd">
                            <div ref="wrap" style="display:none;border:1px solid;width:500px;height:300px;margin:5px 0;position:relative" class="daumaddress">
                                <img src="//t1.daumcdn.net/postcode/resource/images/close.png" id="btnFoldWrap" style="cursor:pointer;position:absolute;right:0px;top:-1px;z-index:1" @click="closePostcode" alt="접기 버튼">
                            </div>
                        </div>
                      </transition>
                      
                  </div>
              </div>
              <div class="payment-way">
                  <p>결제 방법</p>

                  <div :class="{'notclk' : !pays.TF , 'cliked': pays.TF}" v-for="(pays , i) in payway" :key="i" @click="selectway(i)" ref="way">
                      <input type="radio" name="payway" :id="pays.type">
                      <label :for="pays.type">
                           <img :src="require(`@/assets/images/pay/${pays.image}`)" alt="">
                          <p>
                              {{pays.value}}
                          </p>
                      </label>
                  </div>
              </div>
              <div class="payment-fin-box">
                  <div class="payment-fin">
                      <button @click="pays">
                          구매하기
                      </button>
                  </div>
              </div>
          </div>
          <PayModal
            v-if="chkModal"
            @modalchk="moCl"
          />
          <!-- <AddressModal
            v-if="addressModal"
            @addresschk="addresslist"
          /> -->
      </div>
  </div>
</template>


<script>
import PayModal from '@/components/Modal/PayModal.vue'

import { sendphoneCode, phoneCodechk } from "@/api/cart.js";
// import AddressModal from '@/components/Modal/AddressModal.vue'
export default {
    name:'Payment',
    props:{
        user:Object
    },
    data(){
        return{
            // 체크 모달 오픈 유무
            chkModal: false,

            //재품 결제 금액
            itemPay: 0,


            // 배송비
            deliPay: 0,

            // 전체 결제 금액

            totalPay: 0,


            // 전화번호 입력
            tel1:'',
            tel2:'',
            tel3:'',


            // 코드 입력창
            phonCodebtn:true,
            codeSucc: 4,
            codeInput: '',

            // 타이머
            count:179,
            timer: '',
            showTime: '',
            isRunning : false,


            // 주소 입력
            // addressdata: false,
            addcode: '',
            add1:'',
            add2:'',

            // 주소 입력 버튼
            actbtn:[
                {
                    id: 1,
                    clk: true,
                    val: '기본 주소'
                },
                {
                    id: 2,
                    clk: false,
                    val: '회사 주소'
                },
                {
                    id: 3,
                    clk: false,
                    val: '새로운 주소 입력'
                }
            ],

            // 기본 주소로 저장
            addrchk: true,

            // 주소 등록
            notuse: true,

            // 주소 모달창

            addressModal:false,

            // 결제 방법 선택
            payway:[
                {
                    id: 1,
                    name: 'payway',
                    type: 'card',
                    value: '신용/체크',
                    image: 'creditcard.svg',
                    TF: false
                },
                {
                    id: 2,
                    name: 'payway',
                    type: 'kakaoeasy',
                    value: '카카오페이',
                    image: 'payment_icon_yellow_large.png',
                    TF: false
                },
                {
                    id: 3,
                    name: 'payway',
                    type: 'bank',
                    value: '계좌이체',
                    image: 'account.svg',
                    TF: false
                },
                {
                    id: 4,
                    name: 'payway',
                    type: 'account',
                    value: '가상계좌',
                    image: 'bankbook.svg',
                    TF: false
                }
            ],
        }
    },
    watch:{
        tel1(){
            this.tel1 = this.tel1.replace(/[^0-9]/g, '');
        },

        tel2(){
            this.tel2 = this.tel2.replace(/[^0-9]/g, '');
        },

        tel3(){
            this.tel3 = this.tel3.replace(/[^0-9]/g, '');
        },


        
    },
    mounted(){
        // this.totalPrice(),
        // this.noItem(),
        this.showaddress()
        // this.addresscho()
    },
    methods: {

        // 결제 모달창
        // moCl(){
        //     this.chkModal = false
        // },

        // 화면 빈값 에러 표시
        // noItem(){
            
        //     if(this.$route.params.itemtotal == undefined){
        //         alert('잘못된 접근 방식입니다.')
        //         this.$router.push({path:'/cart'})
        //     }
        // },

        // 제품 총액
        // totalPrice(){
        //     this.itemPay = Number(this.$route.params.itemtotal)
        //     this.totalPay = this.itemPay + this.deliPay
        // },


        // 핸드폰 인증코드 요청 화면 
        phonecodeSend(){

            if(this.tel1 != '' && this.tel2 != '' && this.tel3 != ''){
                this.phonCodebtn = false

                const params = new URLSearchParams()
                
                var phoneNum = this.tel1 + this.tel2 + this.tel3

                params.append('userid', this.user.id)
                params.append('phonenumber', phoneNum)

                sendphoneCode(params)
                .then((data) => {
                    console.log(data);
                    if(data.code == "SUCCESS"){
                        if (this.isRunning){
                            clearInterval(this.timer);
                        }
                        else{
                            this.startTimer()
                        }
                    }else{
                        alert('인증 번호 발송에 실패하였습니다. 다시 시도해 주세요.')
                    }
                    
                }).catch((err) => {
                    console.log(err);
                });
            }
            else{
                alert('전화번호 입력란이 비어있습니다.')
            }
            
          
        },

        // 인증 타이머

        startTimer(){

            var mm,ss

            if(this.isRunning == false){
                this.timer = setInterval(() => {
                    mm = parseInt(this.count/ 60 , 10)
                    ss = parseInt(this.count % 60, 10)

                    mm = mm < 10 ? "0" + mm : mm;
                    ss = ss < 10 ? "0" + ss : ss;

                    this.showTime = mm + ':' + ss

                    
                    if(this.codeInput != '' && --this.count > 0){
                        clearInterval(this.timer)
                        this.showTime = ''
                        this.isRunning = false

                    }
                    
                    if (this.codeInput == '' && --this.count < 0) {
                        clearInterval(this.timer)
                        this.showTime = '00:00'
                        this.isRunning = false
                        setTimeout(() => {
                            this.codeSucc = 3
                        }, 1000); 
                    }
                }, 1000);
            }
            else{
                clearInterval(this.timer)
            }
        },


        //번호 인증
        phoneCode(){

            const params = new URLSearchParams()

            params.append('userid', this.user.id)
            params.append('phonekey', this.codeInput)
            console.log(this.user.id);
            console.log(this.codeInput);
            
            phoneCodechk(params)
            .then((data) =>{
                console.log(data);
            })
            .catch((err) => {
                console.log(err);
            })
        },

        // 재인증 
        recount(){
            this.count = 179

            this.startTimer()
        },


        // 결제 방법 선택
        selectway(i){
            // console.log(this.payway[i]);

            if(this.payway[i].TF == false){
                for (let idx = 0; idx < this.payway.length; idx++) {
                    this.payway[idx].TF = false
                }

                this.payway[i].TF = true
            }
        },


        // 기본 주소

        showaddress(){
            this.addcode = '15568'
            this.add1 = '경기도 안산시 상록구 본오로3길 15'
            this.add2 = '401호'
            this.notuse = true
        },

        // 주소찾기
        findAdd(){
            const elementWrap = this.$refs.wrap
            const currentScroll = Math.max(document.body.scrollTop, document.documentElement.scrollTop)

             new window.daum.Postcode({
                oncomplete: (data) => {
                    // 검색결과 항목을 클릭했을때 실행할 코드를 작성하는 부분.
        
                    // 각 주소의 노출 규칙에 따라 주소를 조합한다.
                    // 내려오는 변수가 값이 없는 경우엔 공백('')값을 가지므로, 이를 참고하여 분기 한다.
                    let addr = '' // 주소 변수
                    let addcoder = data.zonecode
        
                    // 사용자가 선택한 주소 타입에 따라 해당 주소 값을 가져온다.
                    if (data.userSelectedType === 'R') { // 사용자가 도로명 주소를 선택했을 경우
                        addr = data.roadAddress
                    } else { // 사용자가 지번 주소를 선택했을 경우(J)
                        addr = data.jibunAddress
                    }
        
                    // 우편번호와 주소 정보를 해당 필드에 넣는다.
                    // document.getElementById('address').value = addr
                    this.add1 = addr
                    this.addcode = addcoder
        
                    // iframe을 넣은 element를 안보이게 한다.
                    // (autoClose:false 기능을 이용한다면, 아래 코드를 제거해야 화면에서 사라지지 않는다.)
                    elementWrap.style.display = 'none'
        
                    // 우편번호 찾기 화면이 보이기 이전으로 scroll 위치를 되돌린다.
                    document.body.scrollTop = currentScroll
                },
                // 우편번호 찾기 화면 크기가 조정되었을때 실행할 코드를 작성하는 부분. iframe을 넣은 element의 높이값을 조정한다.
                onresize: function (size) {
                    elementWrap.style.height = size.height + 'px'
                },
                width: '100%',
                height: '100%'
            }).embed(elementWrap)
            elementWrap.style.display = 'block'
        },
        closePostcode(){
             const elementWrap = this.$refs.wrap
             elementWrap.style.display = 'none'
        },


        // 주소 버튼 클릭 이벤트
        addresscho(i){
            

            if(this.actbtn[i].clk == false){
                for (let idx = 0; idx < this.actbtn.length; idx++) {
                    this.actbtn[idx].clk = false
                    
                }
                this.actbtn[i].clk = true
            }

            if(this.actbtn[i].id == 1){
                console.log('기본 주소 데이터');
                this.addcode = '15568'
                this.add1 = '경기도 안산시 상록구 본오로3길 15'
                this.add2 = '401호'
                this.notuse = true
            }
            if(this.actbtn[i].id == 2) {
                // this.addressModal = true
                console.log('회사 주소 데이터');
                this.addcode = '12345'
                this.add1 = '서울시 강남구 서초로 123134'
                this.add2 = 'kinsight 3층'
                this.notuse = true
            }
            if(this.actbtn[i].id == 3){
                this.addcode = ''
                this.add1 = ''
                this.add2 = ''
                this.notuse = false
            }
        },

        // 주소 모달창
        // addresslist(){
        //     this.addressModal = false
        // },


        // 제품 구매 버튼
        pays(){
            if(this.totalPay == 0){
                alert('구매 완료 되었습니다.')
            }else{
                alert('구매 완료 되었습니다.')
            }
        },
    },
    components:{
        PayModal,
        // AddressModal,
    }

}
</script>

<style lang="scss">
   @import '@/assets/css/cart.scss'
    

</style>