import axios from "axios"

// const local ="http://211.218.206.235:8088"
const server = 'http://ec2-3-36-180-201.ap-northeast-2.compute.amazonaws.com:8088'


// 전화번호 인증 문자
function sendphoneCode(params){
    return axios.post(`${server}/user/smschksend`, params)
    .then(({data}) => {
        return data
    })
}


// 전화번호 인증

function phoneCodechk(params){
    return axios.post(`${server}/user/smschk`, params)
    .then(({data}) => {
        // console.log(data);
        return data
    })
    .catch((err) => {
        console.log(err);
    })
}

export {
    sendphoneCode,
    phoneCodechk,

}